body{margin:0;padding:0}.game-title{font-family:'Monoton', cursive;font-size:35px;text-align:center}.game-body{height:700px;width:1100px;border:2px solid black;margin:auto;background-color:#cfe0df}#game-container{display:flex;flex-direction:column;align-items:center}.header{text-align:center;height:90px;width:480px;background-color:darkslateblue;color:whitesmoke;border:2.2px solid grey;font-family:Arial, Helvetica, sans-serif;font-size:24px;border-radius:15px}.row{display:flex;justify-content:center;align-items:center}#text-snippet{font-size:20px}#play,#playagain{width:100px;height:40px;font-size:16px}#play-container{display:flex;flex-direction:column;align-items:center;justify-content:center;height:250px;width:600px;background-color:#e4e4cd;padding:30px;margin-top:20px;border:3px solid rgba(173,147,147,0.904);border-radius:10px}#result-container{display:flex;flex-direction:column;align-items:center;justify-content:center;height:250px;width:600px;background-color:#e4e4cd;padding:30px;margin-top:20px;border:3px solid rgba(173,147,147,0.904);border-radius:10px}.input{background-color:transparent;margin-top:2rem;width:100%;height:120px;font-size:1rem;border:2px solid #d3d3af;border-radius:10px;outline:none;resize:none;padding:.5rem 1rem;font-size:20px}.timer-container{margin-top:20px;height:50px;width:100px;text-align:center;letter-spacing:3px;border:2px solid darkslateblue;border-radius:10px}.results{display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:20px}#playagain{margin-top:30px}#info{position:fixed;bottom:10}
